li.weglot-hide,.weglot-hide {
    display: none!important
}

.country-selector {
    z-index: 9999;
    text-align: left;
    position: relative;
    display: inline-block;
    width: auto
}

.country-selector a {
    padding: 0 10px;
    outline: none;
    text-decoration: none;
    float: none!important;
    white-space: nowrap;
    font-weight: 400;
    cursor: pointer;
    color: #000;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none
}

.country-selector a:focus {
    outline: none
}

.country-selector ul {
    padding: 0;
    z-index: 1010;
    list-style: none;
    margin: 0
}

.country-selector li {
    margin: 0;
    padding: 0
}

.country-selector.weglot-custom-switcher-ajax {
    display: none
}

.country-selector.weglot-dropdown {
    background-color: #fff
}

.country-selector.weglot-dropdown a,.country-selector.weglot-dropdown span {
    display: block;
    height: 37px;
    line-height: 36px;
    font-size: 13px;
    padding: 0 10px;
    width: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    font-weight: 400
}

.country-selector.weglot-dropdown a:hover,.country-selector.weglot-dropdown span:hover {
    cursor: pointer
}

.country-selector.weglot-dropdown .wgcurrent {
    border: 1px solid #e0e0e0;
    list-style: none;
    display: block;
    margin: 0
}

.country-selector.weglot-dropdown .wgcurrent a,.country-selector.weglot-dropdown .wgcurrent span {
    padding-right: 60px
}

.country-selector.weglot-dropdown .wgcurrent:after {
    display: inline-block;
    position: absolute;
    top: 17px;
    right: 8px;
    width: 13px;
    height: 7px;
    -ms-interpolation-mode: nearest-neighbor;
    image-rendering: -webkit-optimize-contrast;
    image-rendering: -o-pixelated;
    image-rendering: pixelated;
    background: url(../img/wgarrowdown.webp) no-repeat;
    content: "";
    -webkit-transition: all 200ms;
    -o-transition: all 200ms;
    transition: all 200ms;
    -webkit-transform: rotate(-90deg);
    -ms-transform: rotate(-90deg);
    transform: rotate(-90deg)
}

.country-selector.weglot-dropdown ul {
    position: absolute;
    min-width: 100%;
    border: 1px solid #ebeef0;
    background: #fff;
    left: 0;
    top: initial;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    display: none;
    padding: 0
}

.country-selector.weglot-dropdown input:checked~ul {
    display: block
}

.country-selector.weglot-dropdown input:checked~.wgcurrent:after {
    -webkit-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg)
}

.country-selector.weglot-dropdown li {
    width: 100%
}

.country-selector.weglot-dropdown.weglot-invert ul {
    bottom: 38px
}

.country-selector.weglot-dropdown.weglot-invert input:checked~.wgcurrent:after {
    -webkit-transform: rotate(-180deg);
    -ms-transform: rotate(-180deg);
    transform: rotate(-180deg)
}

.country-selector.weglot-default {
    position: fixed;
    bottom: 0;
    right: 40px
}

.country-selector.weglot-inline a:hover {
    text-decoration: underline;
    color: #00a0d2
}

.country-selector.weglot-inline .wgcurrent a {
    text-decoration: underline
}

.country-selector.weglot-inline label {
    margin-bottom: 0;
    display: inline-block;
    vertical-align: middle
}

.country-selector.weglot-inline ul {
    display: inline-block;
    vertical-align: middle
}

.country-selector.weglot-inline li {
    line-height: 1!important;
    display: inline-block;
    margin: 2px 0;
    vertical-align: middle
}

.country-selector.weglot-inline.weglot-default {
    bottom: 5px
