@-webkit-keyframes zoom-in {
    0% {
        opacity: .4;
        -webkit-transform: scale(1.3);
        transform: scale(1.3)
    }

    to {
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1)
    }
}

@keyframes star-zoom-out {
    0% {
        opacity: .4;
        -webkit-transform: scale(.5);
        transform: scale(.5)
    }

    to {
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1)
    }
}

@-webkit-keyframes slider-right-in {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-100%)
    }

    60% {
        opacity: 1;
