    0% {
        -moz-transform: rotateX(180deg);
        opacity: 0
    }

    35% {
        -moz-transform: rotateX(120deg);
        opacity: 0
    }

    65% {
        opacity: 0
    }

    to {
        -moz-transform: rotateX(360deg);
        opacity: 1
    }
}

