    -webkit-transition-duration: .5s;
    transition-duration: .5s
}

.dialog.dialog--open .dialog__content {
    opacity: 1
}

.morph-shape {
    position: absolute;
    width: calc(100% + 2px);
    height: calc(100% + 2px);
    top: -2px;
    left: -2px;
    z-index: -1
}

